import React, { useState, useEffect } from "react";
import "../css/answer.scss";
import { XMLParser } from "fast-xml-parser";
import { fetchGistRawContent } from "../../../services";
import "../css/examanswersnew.scss";
import Plus from "../../../img/svg/plusminus.svg";
import Check from "../../../img/svg/check.svg";
import Dot from "../../../img/svg/dot.svg";
import Header from "../../../components/header";

export default function AnswerNew() {
  const [studentsList, setStudentsList] = useState([]);
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [notFoundError, setNotFoundError] = useState(false);

  useEffect(() => {
    async function fetchStudents() {
      try {
        const grade_02 = await fetchGistRawContent(
          "b442d6acca236b4d159c0db298ce9fc6",
          "2_sinif.xml"
        );

        const students02 = parseStudents(grade_02);

        setStudentsList(students02);
      } catch (error) {
        console.error("Error fetching student data:", error);
        if (error.message.includes("403")) {
          // Handle 403 Forbidden error
          // For example, show a message to the user
          alert(
            "Access to student data is forbidden. Please contact the administrator for assistance."
          );
        }
      }
    }

    fetchStudents();
  }, []); // Run this effect only once on component mount

  function parseStudents(xmlStudentsList) {
    const parser = new XMLParser();
    const parsedXml = parser.parse(xmlStudentsList);
    const { dataroot } = parsedXml;
    const studentsList = dataroot[Object.keys(dataroot)[0]];

    return studentsList;
  }

  function findStudent() {
    const code = document.getElementById("code").value.trim();

    let targetStudent = studentsList.find((s) => s["İş_nömrəsi"] === +code);

    setNotFoundError(!targetStudent);
    setSelectedStudent(targetStudent);
    console.log(targetStudent);
  }

  function handleKeyPress(event) {
    if (event.key === "Enter") {
      findStudent();
    }
  }

  return (
    <>
      <Header />
      <div className="exam__answer">
        <div className="exam__container">
          <h2>25 fevral imtahan nəticələri üçün iş nömrəsini daxil edin</h2>
          <input
            type="text"
            id="code"
            name="code"
            required
            onKeyPress={handleKeyPress}
          />
          <div className="exam__container--btn">
            <button type="button" onClick={findStudent}>
              Nəticəni göstər
            </button>
          </div>
          {selectedStudent && (
            <div id="result" className="exam__result">
              <div className="exam__info">
                <div className="exam__info--name">
                  <div className="name">
                    <span>Ad:</span>
                    {selectedStudent.Ad &&
                      selectedStudent.Ad.charAt(0).toUpperCase() +
                        selectedStudent.Ad.slice(1).toLowerCase()}
                  </div>
                  <div className="surname">
                    <span>Soyad:</span>
                    {selectedStudent.Soyad &&
                      selectedStudent.Soyad.charAt(0).toUpperCase() +
                        selectedStudent.Soyad.slice(1).toLowerCase()}
                  </div>
                </div>
                <div className="exam__info--about">
                  <div>
                    <span>İş nömrəsi:</span>
                    {selectedStudent["İş_nömrəsi"]}
                  </div>
                  <div>
                    <span>Məktəb:</span>
                    {selectedStudent["Məktəb"]}
                  </div>
                  <div>
                    <span>Sinif:</span>
                    {selectedStudent["Sinif"]}
                  </div>
                  <div>
                    <span>Variant:</span>
                    {selectedStudent["Variant"]}
                  </div>
                </div>
                <div className="exam__info--grade">
                  <div className="grade">
                    <h2>Bal:</h2>
                    <h2>{selectedStudent["BAL"]}</h2>
                  </div>
                  <hr />

                  <div className="place">
                    <h3>Sıra:</h3>
                    <h3>{selectedStudent["SIRA"]}</h3>
                  </div>
                </div>
              </div>
              <div className="exam__subject--item">
                <div className="exam__subject--item__name">
                  <h3>Ana dili</h3>
                  <h3>Riyaziyyat</h3>
                  <h3>Xarici dil</h3>
                  <h3>
                    {selectedStudent["İnformatika_Sual_sayı"] ? (
                      <span>İnformatika</span>
                    ) : (
                      <span>Həyat_bilgisi</span>
                    )}
                  </h3>
                  <h3>Məntiq</h3>
                </div>

                <div className="exam__subject">
                  <div className="exam__subject--native">
                    <div className="exam__subject--native__container">
                      <div className="exam__subject--native__close">
                        <h4>Ana dili</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Ana_dili_Doğru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Ana_dili_Cavabınız"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Ana_dili_Status"]}
                        </div>
                      </div>
                      <div className="exam__subject--native__close">
                        <h4>Riyaziyyat</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Riyaziyyat_Doğru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Riyaziyyat_Cavabınız"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Riyaziyyat_Status"]}
                        </div>
                      </div>
                      <div className="exam__subject--native__close">
                        <h4>Xarici dil</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Xarici_dil_Doğru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Xarici_dil_Cavabınız"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Xarici_dil_Status"]}
                        </div>
                      </div>
                      <div className="exam__subject--native__close">
                        <h4>Informatika</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["İnformatika_Doğru_cavablar"] !==
                          "" ? (
                            <span>
                              {selectedStudent["İnformatika_Doğru_cavablar"]}
                            </span>
                          ) : (
                            <span>
                              {selectedStudent["Həyat_bilgisi_Doğru_cavablar"]}
                            </span>
                          )}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["İnformatika_Cavabınız"] !==
                          "" ? (
                            <span>
                              {selectedStudent["İnformatika_Cavabınız"]}
                            </span>
                          ) : (
                            <span>
                              {selectedStudent["Həyat_bilgisi_Cavabınız"]}
                            </span>
                          )}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["İnformatika_Status"] !==
                          "" ? (
                            <span>
                              {selectedStudent["İnformatika_Status"]}
                            </span>
                          ) : (
                            <span>
                              {selectedStudent["Həyat_bilgisi_Status"]}
                            </span>
                          )}
                        </div>
                      </div>
                      <div className="exam__subject--native__close">
                        <h4>Xarici dil</h4>
                        <div className="native__close">
                          <img src={Check} alt="" width={20} />
                          {selectedStudent["Məntiq_Doğru_cavablar"]}
                        </div>
                        <div className="native__close">
                          <img src={Dot} alt="" width={20} />
                          {selectedStudent["Məntiq_Cavabınız"]}
                        </div>
                        <div className="native__close">
                          <img src={Plus} alt="" width={20} />
                          {selectedStudent["Məntiq_Status"]}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <table id="AnswerNew__table">
                <thead>
                  <tr>
                    <th></th>
                    <th>Ana dili</th>
                    <th>Riyaziyyat</th>
                    <th>Xarici dil</th>
                    <th>
                      {selectedStudent["İnformatika_Sual_sayı"] ? (
                        <span>İnformatika</span>
                      ) : (
                        <span>Həyat_bilgisi</span>
                      )}
                    </th>
                    <th>Məntiq</th>
                    <th>CƏMİ</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <th>Sual sayı</th>
                    <td>{selectedStudent["Ana_dili_Sual_sayı"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Sual_sayı"]}</td>
                    <td>{selectedStudent["Xarici_dil_Sual_sayı"]}</td>

                    <td>
                      {selectedStudent["İnformatika_Sual_sayı"] !== "" ? (
                        <span>{selectedStudent["İnformatika_Sual_sayı"]}</span>
                      ) : (
                        <span>
                          {selectedStudent["Həyat_bilgisi_Sual_sayı"]}
                        </span>
                      )}
                    </td>

                    <td>{selectedStudent["Məntiq_Sual_sayı"]}</td>
                    <td>{selectedStudent["CƏMİ_SUAL_SAYI"]}</td>
                  </tr>
                  <tr>
                    <th>DOĞRU</th>
                    <td>{selectedStudent["Ana_dili_Doğru_sayı"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Doğru_sayı"]}</td>
                    <td>{selectedStudent["Xarici_dil_Doğru_sayı"]}</td>

                    <td>
                      {selectedStudent["İnformatika_Doğru_sayı"] !== "" ? (
                        <span>{selectedStudent["İnformatika_Doğru_sayı"]}</span>
                      ) : (
                        <span>
                          {selectedStudent["Həyat_bilgisi_Doğru_sayı"]}
                        </span>
                      )}
                    </td>

                    <td>{selectedStudent["Məntiq_Doğru_sayı"]}</td>
                    <td>{selectedStudent["CƏMİ_DOĞRU"]}</td>
                  </tr>
                  <tr>
                    <th>SƏHV</th>
                    <td>{selectedStudent["Ana_dili_Səhv_sayı"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Səhv_sayı"]}</td>
                    <td>{selectedStudent["Xarici_dil_Səhv_sayı"]}</td>

                    <td>
                      {selectedStudent["İnformatika_Səhv_sayı"] !== "" ? (
                        <span>{selectedStudent["İnformatika_Səhv_sayı"]}</span>
                      ) : (
                        <span>
                          {selectedStudent["Həyat_bilgisi_Səhv_sayı"]}
                        </span>
                      )}
                    </td>

                    <td>{selectedStudent["Məntiq_Səhv_sayı"]}</td>
                    <td>{selectedStudent["CƏMİ_SƏHV"]}</td>
                  </tr>
                  <tr>
                    <th>QİYMƏT</th>
                    <td>{selectedStudent["Ana_dili_Xalis_qiyməti"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Xalis_qiyməti"]}</td>
                    <td>{selectedStudent["Xarici_dil_Xalis_qiyməti"]}</td>

                    <td>
                      {selectedStudent["İnformatika_Xalis_qiyməti"] !== "" ? (
                        <span>
                          {selectedStudent["İnformatika_Xalis_qiyməti"]}
                        </span>
                      ) : (
                        <span>
                          {selectedStudent["Həyat_bilgisi_Xalis_qiyməti"]}
                        </span>
                      )}
                    </td>

                    <td>{selectedStudent["Məntiq_Xalis_qiyməti"]}</td>
                    <td>{selectedStudent["CƏMİ_XALİS_qiyməti"]}</td>
                  </tr>
                  <tr>
                    <th>BAL</th>
                    <td>{selectedStudent["Ana_dili_Nisbi_bal"]}</td>
                    <td>{selectedStudent["Riyaziyyat_Nisbi_bal"]}</td>
                    <td>{selectedStudent["Xarici_dil_Nisbi_bal"]}</td>

                    <td>
                      {selectedStudent["İnformatika_Nisbi_bal"] !== "" ? (
                        <span>
                          {selectedStudent["İnformatika_Nisbi_bal"]}
                        </span>
                      ) : (
                        <span>
                          {selectedStudent["Həyat_bilgisi_Nisbi_bal"]}
                        </span>
                      )}
                    </td>

                    <td>{selectedStudent["Məntiq_Nisbi_bal"]}</td>
                    <td>{selectedStudent["BAL"]}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          )}
          {notFoundError && (
            <div className="error">Bu kodu olan tələbə tapılmadı.</div>
          )}
        </div>
      </div>
    </>
  );
}
